@font-face {
  font-family: "SF-UI-Text-Ultralight";
  src: url("./assets/SF-UI/SF-UI-Display-Ultralight.otf") format("opentype"); }

@font-face {
  font-family: "SF-UI-Display-Light";
  src: url("./assets/SF-UI/SF-UI-Display-Light.otf") format("opentype"); }

@font-face {
  font-family: "SF-UI-Text-Light";
  src: url("./assets/SF-UI/SF-UI-Text-Light.otf") format("opentype"); }

@font-face {
  font-family: "SF-UI-Display-Thin";
  src: url("./assets/SF-UI/SF-UI-Display-Thin.otf") format("opentype"); }

body, button, div, span {
  margin: 0;
  padding: 0;
  font: 14px;
  font-family: "SF-UI-Text-Ultralight";
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  outline: none; }

span, li, a, p {
  letter-spacing: .7px; }

a, p {
  text-decoration: none;
  color: #EDF0F2; }



