.ButtonWrapper {
  border: 1px solid #197a59;
  margin: 0 35px;
  background-color: transparent; }
  .ButtonWrapper:hover {
    background: white;
    -webkit-box-shadow: 0px 0px 50px 10px #197a59;
            box-shadow: 0px 0px 50px 10px #197a59; }
    .ButtonWrapper:hover .MainButton {
      background-color: #1d222c; }
      .ButtonWrapper:hover .MainButton a {
        color: rgba(223, 223, 223, 0.86); }

.MainButton {
  border: 1px solid #197a59;
  background-color: transparent;
  margin: 1px;
  font-size: 20px;
  letter-spacing: 2px;
  outline: none;
  border-radius: 0; }
  .MainButton a {
    text-decoration: none;
    color: rgba(104, 104, 104, 0.86);
    min-width: 158px;
    display: inline-block;
    padding: 12px 0px; }

.closeButton {
  position: absolute;
  right: 10%;
  margin: 20px 0;
  width: 40px;
  height: 40px;
  background-color: transparent;
  border: 1px solid orange;
  color: black;
  opacity: .6;
  display: block;
  cursor: pointer; }

.backBtn {
  position: absolute;
  font-size: 15px; }



